
import LoadMore from 'vux/src/components/load-more/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Datetime from 'vux/src/components/datetime/index.vue'

import Scroll from './pulldown'

export default {
components: {
LoadMore,
Cell,
Group,
Badge,
Divider,
Flexbox,
FlexboxItem,
Datetime,
'v-scroll': Scroll
},
data () {
return {
value1: '2015-11-12',
index: 0,
loadmore: false,
loadbottom: false,
orderList: [],
loadingText: '',
pageNum: 1, // 
pageSize: 10,  // 
listdata: [], // 
downdata: []  // 
}
},
created: function () {
this.fetchData(this.pageNum, this.pageSize)
},
watch: {

'$route': 'fetchData'
},
methods: {
fetchData: function (pageNum, pageSize) {
this.loadmore = true
var params = 'pageNum=' + pageNum + '&pageSize=' + pageSize + '&id=' + this.$route.params.id
this.$http.post(this.domain + '/api/anon/car/getCarOrderList', params).then((response) => {
this.orderList = response.data.data
this.loadmore = false
}).catch((response) => {
this.$vux.loading.hide()
this.$vux.toast.text('!', 'middle')
})
},
onRefresh (done) {
this.loadbottom = false
this.orderList = []
this.pageNum = 1
this.fetchData(1, 10)
done()
},
onInfinite (done) {
if (this.loadbottom === true) {
done()
return
} else {
this.loadmore = true
this.pageNum++
let pageNum = this.pageNum
var params = 'pageNum=' + pageNum + '&pageSize=' + this.pageSize + '&id=' + this.$route.params.id
this.$http.post(this.domain + '/api/auth/car/getCarOrderList', params).then((response) => {
this.downdata = response.data.data
this.orderList = this.orderList.concat(this.downdata)
if (this.downdata.length === 0) {
this.loadbottom = true
}
this.loadmore = false
done()
}).catch((response) => {
this.$vux.loading.hide()
this.$vux.toast.text('!', 'middle')
})
}
}
}
}



